<template>
  <b-overlay
    :show="isLoading"
    :variant="variant"
    :opacity="opacity"
    spinner-variant="primary"
    spinner-type="grow"
    spinner-small
    rounded="sm"
  >
    <b-card
      :title="team.name"
      aria-hidden="true"
      class="mb-0"
    >
      <b-card-text>{{ team.description }}</b-card-text>
      <b-card-sub-title>Unidades</b-card-sub-title>
      <b-row class="mt-2">
        <b-col
          v-for="unit in unitsList"
          :key="unit.id"
          cols="6"
          sm="4"
          md="3"
          lg="2"
          xl="2"
        >
          <b-form-checkbox
            v-model="unitsSelected"
            :value="unit.id"
            class="custom-control-primary"
            @change="() => checked(unit.id)"
          >
            {{ unit.plate }}
          </b-form-checkbox>
        </b-col>
      </b-row>
    </b-card>
  </b-overlay>
</template>

<script>
import { ref } from '@vue/composition-api'
import {
  BCard,
  BCardText,
  BCardSubTitle,
  BOverlay,
  BRow,
  BCol,
  BFormCheckbox,
} from 'bootstrap-vue'
import useCatalogs from '@/composables/useCatalogs'
import useTeam from './composables/useTeam'

export default {
  components: {
    BCard,
    BCardText,
    BCardSubTitle,
    BOverlay,
    BRow,
    BCol,
    BFormCheckbox,
  },
  setup(__, ctx) {
    const { id } = ctx.root.$route.params
    const {
      isLoading,
      getTeamWithUnits,
      team,
      unitsSelected,
      addUnit,
      removeUnit,
    } = useTeam(ctx)

    const { unitsList, getUnitsReadyToUse } = useCatalogs()

    const variant = ref('light')
    const opacity = ref(0.8)

    const checked = unitId => {
      const selected = unitsSelected.value.includes(unitId)
      if (selected) {
        addUnit(unitId)
      } else {
        removeUnit(unitId)
      }
    }

    getUnitsReadyToUse()
    getTeamWithUnits(id)

    return {
      isLoading,
      team,
      unitsList,
      unitsSelected,
      checked,
      variant,
      opacity,
    }
  },
}
</script>

<style></style>
