import { useToast } from 'vue-toastification/composition'
import ToastificationContent from '@/@core/components/toastification/ToastificationContent.vue'
import TeamService from '@/services/TeamService'
import { reactive, ref } from '@vue/composition-api'

const useTeam = () => {
  const toast = useToast()
  const isSaving = ref(false)
  const isLoading = ref(false)
  const blank = {
    id: '',
    name: '',
    description: '',
    units: [],
    status: true,
  }
  const states = reactive({
    name: {
      isInvalid: false,
      message: '',
    },
  })
  const isSidebarActive = ref(false)

  const team = reactive(blank)

  const resetData = () => {
    team.id = ''
    team.name = ''
    team.description = ''
    team.status = true
    team.units = []

    states.name.isInvalid = false
    states.name.message = ''
  }

  const saveNew = async () => {
    isSaving.value = true
    try {
      const { id, units, ...rest } = team
      const unitsList = units.map(u => u.id)
      await TeamService.create({ ...rest, units: unitsList })
      isSaving.value = false
      isSidebarActive.value = false
      setTimeout(() => {
        resetData()
      }, 2000)
      toast({
        component: ToastificationContent,
        props: {
          title: 'Registrado correctamente!!',
          icon: 'CheckCircleIcon',
          variant: 'success',
        },
      })
      return true
    } catch (error) {
      isSaving.value = false
      if (error.response.data.statusCode === 400) {
        states.name.isInvalid = true
        states.name.message = error.response.data.message
      }
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al Actualizar',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
      throw error
    }
  }

  const edit = async id => {
    isLoading.value = true
    try {
      const response = await TeamService.getById(id)
      const {
        name,
        description,
        status,
        units,
      } = response.data
      team.id = response.data.id
      team.name = name
      team.description = description
      team.status = status
      // team.units = units ? units.map(d => d.id) : []
      team.units = units
      isLoading.value = false
      isSidebarActive.value = true
    } catch (error) {
      isLoading.value = false
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al Consultar registro',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
      throw error
    }
  }

  const update = async () => {
    isSaving.value = true
    try {
      const { id, units, ...rest } = team
      const unitsList = units.map(u => u.id)
      await TeamService.update(team.id, { units: unitsList, ...rest })
      isSaving.value = false
      isSidebarActive.value = false
      setTimeout(() => {
        resetData()
      }, 2000)
      toast({
        component: ToastificationContent,
        props: {
          title: 'Actualizado Correctamente!',
          icon: 'CheckCircleIcon',
          variant: 'success',
        },
      })
    } catch (error) {
      isSaving.value = false
      if (error.response.data.statusCode === 400) {
        states.name.isInvalid = true
        states.name.message = error.response.data.message
      }
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al Actualizar',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
      throw error
    }
  }

  const save = async () => {
    if (team.id !== '') {
      await update()
    } else {
      await saveNew()
    }
  }

  return {
    isSaving,
    isSidebarActive,
    states,
    team,
    // methods
    save,
    resetData,
    edit,
    update,
  }
}
export default useTeam
